import React from 'react';
import { Carousel, Col, Container, Row } from 'react-bootstrap';
import { Link, useNavigate } from 'react-router-dom';
import '../style/Description.scss';
import { racineURL, root } from '../util/util';

const Galerie = () => {
    const navigate = useNavigate();

    const handleClick = () => {
        navigate('/disponibilites');
    }


    return (
        <Container id='infos' fluid className='galerie'>
            <h1 className='m-5 text-uppercase'>Vos vacances chez nous !</h1>
            <div className='trait'><hr /></div>
            <Container fluid className='sectionGalerie'>
                <div>
                    <Container className='ligne'>
                        <Row as='span'>
                            <Col data-aos="slide-right" sm={1} md={12} lg={6} as='span' className='leftBlock'>
                                <div className="blockText">
                                    <h2 className='mb-3'>Résidence l'Etang</h2>
                                    <div className='trait-subtitle'><hr /></div>
                                    <p>
                                        Nous vous proposons un magnifique séjour à la Résidence de l'ETANG, Située à cap coudalere au coeur du village du Barcarès,
                                         dans la région des pyrénées orientales. Excellent lieu de vacance se trouvant entre le port Leucate et Cannet en Roussillon,
                                          proche de Perpignan, Narbonne et l'Espagne.
                                    </p>
                                    <button>Qui sommes-nous ?</button>
                                </div>
                            </Col>
                            <Col data-aos="fade-in" data-aos-duration='3000' sm={1} md={12} lg={6}  as='span' className='rigthBlock'>
                                <Carousel fade indicators={false}>
                                    <Carousel.Item>
                                        <img className="d-block" src={`${racineURL}/out7.jpg`} alt="exterieur" />
                                    </Carousel.Item>
                                    <Carousel.Item>
                                        <img className="d-block" src={`${racineURL}/out8.jpg`} alt="exterieur" />
                                    </Carousel.Item>
                                </Carousel>
                            </Col>
                        </Row>
                    </Container>
                </div>
                <div>
                    <Container className='ligne'>
                        <Row as='span' data-aos="fade-up">
                            <Col sm={1} md={12} lg={6} as='span' className='leftBlock'>
                                <Carousel fade controls={false} indicators={false}>
                                    <Carousel.Item>
                                        <img className="d-block" src={`${racineURL}/out1.jpg`} alt="exterieur" />
                                    </Carousel.Item>
                                    <Carousel.Item>
                                        <img className="d-block" src={`${racineURL}/sport2.jpg`} alt="exterieur" />
                                    </Carousel.Item>
                                    <Carousel.Item>
                                        <img className="d-block" src={`${racineURL}/out2.jpg`} alt="exterieur" />
                                    </Carousel.Item>
                                    <Carousel.Item>
                                        <img className="d-block" src={`${racineURL}/sport1.jpg`} alt="exterieur" />
                                    </Carousel.Item>
                                    <Carousel.Item>
                                        <img className="d-block" src={`${racineURL}/out3.jpg`} alt="exterieur" />
                                    </Carousel.Item>
                                </Carousel>

                            </Col>
                            <Col sm={1} md={12} lg={6} as='span' className='rigthBlock'>
                                <div className="blockText">
                                    <h2 className='mb-3'>Offre du site</h2>
                                    <div className='trait-subtitle'><hr /></div>
                                    <p>
                                        Un espace aquatique avec 3 piscines chauffées de pâques à mi-octobre.
                                        En plus d'un terrain de basket et des terrains multisports, vous avez quatres cours de tennis, une table de ping-pong et un espace fitness.
                                        En juillet et août, il y'a des animations sportives proposées par divers coachs sportifs.
                                    </p>
                                    <p>
                                        Sur la place martinique, vous avez plusieurs commerces : Boulangerie, restaurants, tabacs, épiceries, bars, glaciers, etc... <br />
                                        Pour la baignade, vous avez la plage du lac marin pour les veliplanchistes. Puis à 2 minutes en voiture (avec parking gratuit et facilement accessible),
                                        vous avez la mer méditerranée avec la plus belle plage de sable fin de Barcarès. Ppour les piétons et les cyclistes, vous disposez d'une piste cyclable de 1500m de long.
                                    </p>
                                    
                                </div>
                            </Col>
                        </Row>
                    </Container>
                </div>
                <div>
                    <Container className='ligne'>
                        <Row as='span'>
                            <Col data-aos="slide-right" sm={1} md={12} lg={6} as='span' className='leftBlock'>
                                <div className="blockText">
                                    <h2 className='mb-3'>Intérieur</h2>
                                    <div className='trait-subtitle'><hr /></div>
                                    <p>
                                        Magnifique espace intérieur à partager en couple ou en famille, très lumineux et suffisamment aéré.
                                        Vous disposez d'un balcon avec vue directe sur la plage. Pas besoin de vous en faire pour la connexion wifi car le site offre un 
                                        Hotspot wifi extérieur gratuit. Un coin cuisine, une micro-ondes, un barbecue électrique, tout ce dont vous aurez besoin pour passer 
                                        un superbe moment seul ou accompagné.
                                    </p>
                                    <button onClick={handleClick}> Disponibilités {'>>'}</button>
                                </div>
                            </Col>
                            <Col data-aos="slide-left" sm={1} md={12} lg={6} as='span' className='rigthBlock'>
                                <Carousel fade indicators={false}>
                                    <Carousel.Item>
                                        <img className="d-block" src={`${racineURL}/appart/int1.jpg`} alt="interieur" />
                                    </Carousel.Item>
                                    <Carousel.Item>
                                        <img className="d-block" src={`${racineURL}/appart/int2.jpg`} alt="interieur" />
                                    </Carousel.Item>
                                    <Carousel.Item>
                                        <img className="d-block" src={`${racineURL}/appart/int3.jpg`} alt="interieur" />
                                    </Carousel.Item>
                                    <Carousel.Item>
                                        <img className="d-block" src={`${racineURL}/appart/int4.jpg`} alt="interieur" />
                                    </Carousel.Item>
                                    <Carousel.Item>
                                        <img className="d-block" src={`${racineURL}/appart/int5.jpg`} alt="interieur" />
                                    </Carousel.Item>
                                    <Carousel.Item>
                                        <img className="d-block" src={`${racineURL}/appart/int6.jpg`} alt="interieur" />
                                    </Carousel.Item>
                                    <Carousel.Item>
                                        <img className="d-block" src={`${racineURL}/appart/int7.jpg`} alt="interieur" />
                                    </Carousel.Item>
                                    <Carousel.Item>
                                        <img className="d-block" src={`${racineURL}/appart/int8.jpg`} alt="interieur" />
                                    </Carousel.Item>
                                </Carousel>
                            </Col>
                        </Row>
                    </Container>
                </div>
            </Container>
        </Container>
    );
};

export default Galerie;